
@font-face {
  font-family: 'Bebas Neue';
  font-style: normal;
  font-weight: normal;
  src: local('Bebas Neue'), url('/static/BebasNeue.woff') format('woff');
}
body {
  -webkit-user-select: none;
  -moz-user-select: -moz-none;
  -ms-user-select: none;
  user-select: none;
  background: #242431;
  color: white;
  font-family: sans-serif;
}
.wrapper {
  max-width: 640px;
  margin: 0 auto;
}
.wrapper > * {
  margin: 0 auto;
  text-align: center;
}
.header {
  font-family: "Bebas Neue", "Impact", sans-serif;
  font-size: 4em;
}
.creampie, .yellowface {
  width: 200px;
  height: 200px;
  display: block;
  cursor: pointer;
  -webkit-animation-duration: 0.9s;
          animation-duration: 0.9s;
  -webkit-animation-fill-mode: both;
          animation-fill-mode: both;
}
.yellowface {
  -webkit-animation-duration: 0.25s;
          animation-duration: 0.25s;
  -webkit-animation-name: fucc;
          animation-name: fucc;
  -webkit-animation-iteration-count: infinite;
          animation-iteration-count: infinite;
}
@-webkit-keyframes fucc {
from {
    -webkit-transform: rotate(0);
            transform: rotate(0);
}
to {
    -webkit-transform: rotate(359deg);
            transform: rotate(359deg);
}
}
@keyframes fucc {
from {
    -webkit-transform: rotate(0);
            transform: rotate(0);
}
to {
    -webkit-transform: rotate(359deg);
            transform: rotate(359deg);
}
}
@-webkit-keyframes virus {
from {
    color: white;
    background: #ff4444;
}
to {
    color: red;
    background: #ffffff;
}
}
@keyframes virus {
from {
    color: white;
    background: #ff4444;
}
to {
    color: red;
    background: #ffffff;
}
}
@-webkit-keyframes pulse {
from {
    -webkit-transform: scale3d(1, 1, 1);
            transform: scale3d(1, 1, 1);
}
50% {
    -webkit-transform: scale3d(1.05, 1.05, 1.05);
            transform: scale3d(1.05, 1.05, 1.05);
}
to {
    -webkit-transform: scale3d(1, 1, 1);
            transform: scale3d(1, 1, 1);
}
}
@keyframes pulse {
from {
    -webkit-transform: scale3d(1, 1, 1);
            transform: scale3d(1, 1, 1);
}
50% {
    -webkit-transform: scale3d(1.05, 1.05, 1.05);
            transform: scale3d(1.05, 1.05, 1.05);
}
to {
    -webkit-transform: scale3d(1, 1, 1);
            transform: scale3d(1, 1, 1);
}
}
.pulse {
  -webkit-animation-name: pulse;
          animation-name: pulse;
}
.slaves {
  display: block;
  list-style-type: none;
  padding: 0;
  margin: 0;
}
.slaves li {
  position: relative;
  background: rgba(0,0,0,0.25);
  padding: 5px;
  margin: 5px;
}
.row {
  display: block;
}
.seg {
  margin-right: 10px;
}
.buy.active {
  background: rgba(0,0,0,0.25);
  cursor: pointer;
  visibility: visible;
}
.buy {
  padding: 20px 15px;
  display:table-cell;
  vertical-align: middle;
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  visibility: hidden;
}
.icon-creampie {
  display:inline-block;
  background: url('/static/creampie.png');
  background-size: contain;
  width: 0.9em;
  height: 0.9em;
}
.hidden {
  display: none;
}
.count {
  font-weight: bold;
}
.footer {
  margin-top: 20px;
  font-size: 0.75em;
}
.smaller {
  font-size: 0.75em;
}
.mtop {
  margin-top: 5px;
}
.virus {
  background: #ff4444;
  -webkit-animation-duration: 0.25s;
          animation-duration: 0.25s;
  -webkit-animation-name: virus;
          animation-name: virus;
  -webkit-animation-iteration-count: infinite;
          animation-iteration-count: infinite;
}
a, a:visited {
  color: #4444ff;
}



